* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #F1F6EC;
  margin: 0 10rem;
  font-family: -apple-system, BlinkMacSystemFont, 'Unbounded', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card {
  animation: all 0.2s ease-out;
}

.card:hover {
  border: 4px solid #7ECC66;
  background-color: #7ECC66;
  border-radius: 2.4rem;
}

.category-btn {
  animation: all 0.2s ease-out;
}

.category-btn:hover {
  opacity: 0.8;
}

a {
  color: #1F1F1F;
  text-decoration: none;
  margin: 0 0.5rem;
  text-align: center;
}
